<template>
  <div class="happy">
    <audio controls="controls" muted preload="auto" id="audio">
      <source src="../assets/music/happy.wav" type="audio/ogg" />
      <source src="../assets/music/happy.wav" type="audio/mpeg" />
    </audio>
    <audio controls="controls" muted preload="auto" id="audio1">
      <source src="../assets/music/circulation.wav" type="audio/ogg" />
      <source src="../assets/music/circulation.wav" type="audio/mpeg" />
    </audio>
    <div class="bg-warp" ref="bg-warp">
      <div class="bg bg_1">
        <div class="lll" @click="go">L L L</div>
      </div>
      <div class="bg bg_2">
        <div class="door-warp">
          <div class="left-door" ref="leftDoor"></div>
          <div class="right-door" ref="rightDoor"></div>
        </div>
      </div>
      <div class="bg bg_3">
        <div class="girl" :class="{ 'girl-rotate': girlRotate }"></div>
        <!-- <div
          v-for="(item, index) in 20"
          :key="index"
          class="flower"
          :style="{
            left: '10px',
            height: '22px',
            width: '22px',
            background: `url(../assets/images/snowflake/snowflake${(index % 6) +
              1}.png) no-repeat`,
            backgroundSize: '100% 100%'
          }"
        ></div> -->
      </div>
    </div>

    <div class="boy" @click="go" ref="boy"></div>
    <div class="boy1" ref="boy1"></div>
    <div class="boy2" ref="boy2" :class="{ 'boy-rotate': boyRotate }"></div>
  </div>
</template>

<script>
export default {
  name: "Happy",
  data() {
    return {
      girlRotate: false,
      boyRotate: false,
      audioPlayer: new Audio()
    };
  },
  created() {},
  mounted() {},
  methods: {
    go() {
      let audio = document.getElementById("audio");
      audio.play();
      this.$anime({
        targets: this.$refs["boy"],
        left: 600,
        duration: 4500,
        easing: "linear",
        complete: () => {
          this.moveBg();
        }
      });
    },
    go2() {
      this.$anime({
        targets: this.$refs["boy1"],
        left: 162,
        duration: 2000,
        easing: "linear",
        complete: () => {}
      });
    },
    go3() {
      this.$anime({
        targets: this.$refs["boy1"],
        left: 300,
        top: 300,
        duration: 2000,
        easing: "linear",
        complete: () => {
          this.go4();
        }
      });
    },
    go4() {
      this.$anime({
        targets: this.$refs["boy1"],
        left: 406,
        duration: 2000,
        easing: "linear",
        complete: () => {
          this.$refs["boy1"].style.animationPlayState = "paused";
          this.$refs["boy1"].style.opacity = 0;
          this.$refs["boy2"].style.opacity = 1;
          setTimeout(() => {
            this.rotate();
          }, 2000);
        }
      });
    },
    moveBg2() {
      this.$anime({
        targets: this.$refs["bg-warp"],
        translateX: "-2048px",
        duration: 6000,
        easing: "linear",
        complete: () => {
          this.go3();
        }
      });
    },
    closeDoor() {
      this.$anime({
        targets: this.$refs.leftDoor,
        left: [-65, 0],
        duration: 1000,
        easing: "linear",
        complete: () => {}
      });
      this.$anime({
        targets: this.$refs.rightDoor,
        left: [130, 65],
        duration: 1000,
        easing: "linear",
        complete: () => {}
      });
    },
    openDoor() {
      this.$anime({
        targets: this.$refs.leftDoor,
        left: -65,
        duration: 1000,
        easing: "linear",
        complete: () => {}
      });
      this.$anime({
        targets: this.$refs.rightDoor,
        left: 130,
        duration: 1000,
        easing: "linear",
        complete: () => {
          this.comeIn();
        }
      });
    },
    moveBg() {
      this.$anime({
        targets: this.$refs["bg-warp"],
        translateX: "-926px",
        duration: 6000,
        easing: "linear",
        complete: () => {
          this.openDoor();
        }
      });
    },
    comeIn() {
      this.$anime({
        targets: this.$refs["boy"],
        // left: 700,
        top: 350,
        opacity: 0,
        duration: 2000,
        easing: "linear",
        complete: () => {
          setTimeout(() => {
            this.goOut();
          }, 1000);
        }
      });
    },
    goOut() {
      this.$anime({
        targets: this.$refs["boy1"],
        top: [350, 390],
        opacity: [0, 1],
        duration: 2000,
        easing: "linear",
        complete: () => {
          this.closeDoor();
          this.go2();
          this.moveBg2();
        }
      });
    },
    rotate() {
      this.girlRotate = true;
      this.boyRotate = true;
      setTimeout(() => {
        let audio = document.getElementById("audio1");
        audio.play();
      }, 2000);
    }
  }
};
</script>

<style scoped>
/* 普通慢走 */
@keyframes person-slow {
  0% {
    background-position: -0px -291px;
  }
  25% {
    background-position: -602px -0px;
  }
  50% {
    background-position: -302px -291px;
  }
  75% {
    background-position: -151px -291px;
  }
  100% {
    background-position: -0px -291px;
  }
}

@-webkit-keyframes person-slow {
  0% {
    background-position: -0px -291px;
  }
  25% {
    background-position: -602px -0px;
  }
  50% {
    background-position: -302px -291px;
  }
  75% {
    background-position: -151px -291px;
  }
  100% {
    background-position: -0px -291px;
  }
}

@-moz-keyframes person-slow {
  0% {
    background-position: -0px -291px;
  }
  25% {
    background-position: -602px -0px;
  }
  50% {
    background-position: -302px -291px;
  }
  75% {
    background-position: -151px -291px;
  }
  100% {
    background-position: -0px -291px;
  }
}
@keyframes person-walk {
  0% {
    left: 0;
  }
  100% {
    left: 200px;
  }
}
@keyframes bg-move {
  0% {
    transform: translateX(0px);
  }
  100% {
    transform: translateX(-1024px);
  }
}
@keyframes person-slow1 {
  0% {
    background-position: -755px -0px;
  }
  25% {
    background-position: -302px -0px;
  }
  50% {
    background-position: -906px -0px;
  }
  75% {
    background-position: -302px -0px;
  }
  100% {
    background-position: -755px -0px;
  }
}
@keyframes boy-rotate {
  0% {
    background-position: -453px -291px;
  }
  25% {
    background-position: -906px -291px;
  }
  50% {
    background-position: -755px -291px;
  }
  75% {
    background-position: -604px -291px;
  }
  100% {
    background-position: -604px -291px;
  }
}
@keyframes girl-rotate {
  0% {
    background-position: -906px -0px;
  }
  25% {
    background-position: -0px -0px;
  }
  50% {
    background-position: -302px -0px;
  }
  75% {
    background-position: -453px -0px;
  }
  100% {
    background-position: -604px -0px;
  }
}
.happy {
  width: 1024px;
  height: 768px;
  background: url("../assets/images/QixiA.png");
  background-size: 100% 100%;
  overflow: hidden;
  position: relative;
}
.bg-warp {
  position: relative;
  width: 3072px;
  height: 800px;
  display: flex;
  /* transition: all 4s; */
  /* transform: translateX(-2048px); */
  /* animation: bg-move 1000000s linear 1 forwards; */
}
.bg {
  position: relative;
  width: 1024px;
  height: 800px;
}
.bg_1 {
  background: url("../assets/images/QixiA.png");
  background-size: 100% 100%;
}
.bg_1 .lll {
  position: absolute;
  text-align: center;
  line-height: 41px;
  top: 250px;
  left: 541px;
  height: 41px;
  width: 81px;
  font-size: 16px;
  font-family: PingFangSC-Semibold, PingFang SC;
  font-weight: 600;
  color: rgba(255, 255, 255, 1);
  background: rgba(182, 216, 220, 1);
}
.bg_2 {
  background: url("../assets/images/bg2.png");
  background-size: 100% 100%;
}
.bg_3 {
  background: url("../assets/images/QixiC.png");
  background-size: 100% 100%;
}
.bg_2 .door-warp {
  position: absolute;
  height: 188px;
  width: 130px;
  overflow: hidden;
  top: 386px;
  left: 534px;
}
.bg_2 .left-door {
  position: absolute;
  background: url("../assets/images/door-left.png");
  background-size: 100% 100%;
  height: 188px;
  width: 65px;
  /* top: 386px;
  left: 534px; */
}
.bg_2 .right-door {
  position: absolute;
  background: url("../assets/images/door-right.png");
  background-size: 100% 100%;
  height: 188px;
  width: 65px;
  /* top: 386px; */
  left: 65px;
}
.boy {
  position: absolute;
  top: 390px;
  left: -120px;
  width: 151px;
  height: 291px;
  background: url("../assets/images/boy.png") -0px -291px no-repeat;
  animation: person-slow 950ms steps(1, start) infinite;
  /* transition: all linear 1s; */
  transform: scale(0.7);
}
.boy1 {
  position: absolute;
  width: 151px;
  height: 291px;
  left: 600px;
  top: 350px;
  background: url("../assets/images/boy.png") -755px -0px no-repeat;
  animation: person-slow1 950ms steps(1, start) infinite;
  opacity: 0;
  transform: scale(0.7);
}
.boy2 {
  position: absolute;
  width: 151px;
  height: 291px;
  left: 406px;
  top: 300px;
  background: url("../assets/images/boy.png") -453px -291px no-repeat;
  opacity: 0;
  transform: scale(0.7);
}
.girl {
  position: absolute;
  top: 300px;
  left: 492px;
  width: 151px;
  height: 291px;
  background: url("../assets/images/girl.png") -755px 0px no-repeat;
  /* animation: person-slow 950ms steps(1, start) infinite; */
  /* transition: all linear 1s; */
  transform: scale(0.7);
}
.boy-rotate {
  background: url("../assets/images/boy.png") -453px -291px no-repeat;
  animation: boy-rotate 800ms steps(1, start) 1 forwards;
}
.girl-rotate {
  background: url("../assets/images/girl.png") -906px -0px no-repeat;
  animation: girl-rotate 800ms steps(1, start) 1 forwards;
}
.flower {
  position: absolute;
  height: 22px;
  width: 22px;
}
#audio {
  position: absolute;
  top: -200px;
}
#audio1 {
  position: absolute;
  top: -200px;
}
</style>



